.category-selector {

    &__flex {
        display: flex;
        justify-content: space-between;
    }

    ul {
        padding: 0;

        li {
            position: relative;
            list-style-image: none;
            
            .current {
                @include SourceSansProSemibold();
                @include rem('margin', 3px 0);
                
                a {
                    border-color: $link_color;
                }
            }

            ul {
                @include rem('padding-left', 24px);
                margin: 0;

                // blue lines to the categories
                li {
                    &:before {
                        content: "";
                        position: absolute;
                        @include rem('top', -3px);
                        @include rem('bottom', -3px);
                        @include rem('left', -13px);
                        @include rem('width', 1px);
                        background-color: $almond;
                    }

                    &:after {
                        content: "";
                        position: absolute;
                        @include rem('top', 13px);
                        @include rem('left', -12px);
                        @include rem('height', 1px);
                        @include rem('width', 12px);
                        background-color: $almond;
                    }
                    
                    ul li:last-child {
                        &:before {
                            @include rem('bottom', 16px);
                        }
                    }
                }
            }
        }
    }
    
    ul > li > span {
        display: block;
        background-color: $janna;
        @include rem('padding', 1px 7px);
        @include rem('margin-bottom', 3px);
        @include rem('border-left', 3px solid $main_red);
    }
    ul > li > ul > li > span {
        background-color: $rum_swizzle;
        border-left: 0 none;
    }
    ul > li > ul > li > ul {
        @include rem('margin-bottom', 3px);
    }
    ul > li > ul > li > ul > li > a {
        display: inline-block;
        @include rem('padding', 0 7px 1px);
        @include border-radius(3px);
        @include rem('border', 1px solid transparent);
        @include transition($property: border-color, $duration: 100ms);
        
        &:hover,
        &:active {
            border-color: $link_color;
            text-decoration: none;
        }
    }
}