// Panels
.panel_container{
    position: relative;
}

.panel {
    position: absolute;
    right: 0;
    z-index: 15;
    background-color: $white;
    @include rem('padding', 2px 4px 0px 0px);
    @include rem('width', 350px);

    &__header {
        margin: 0;
        @include SourceSansProRegular();
        @include rem('font-size', 16px);
        line-height: (32px / 16px);
        float: right;

        &__toggle {
            color: $link_color;
            display: flex;
            justify-content: space-between;
            @include rem('padding-right', 10px);

            &:hover {
                color: $link_color;
                text-decoration: none;
            }
        }
    }

    &--orgenhet {
        @include rem('top', -88px);
    }

    .arrow-icon {
        @include single-arrow-icon($direction: 'down', $color: $link-color, $size: 'large');
        @include rem('top', 16px);
        @include rem('margin-left', 6px);
    }

    &__body {
        clear: both;
        @include rem('border-top', 1px solid $almond);
        @include rem('padding-top', 10px);
        @include rem('margin', 10px 10px);
    }

    &--open {
        @include rem-box-shadow(0 1px 4px rgba($black, 40/100));
        @include border-radius(0 0 $base_border_radius $base_border_radius);

        .arrow-icon {
            @include single-arrow-icon($direction: 'up', $color: $link-color, $size: 'large');
            @include rem('top', 16px);
            @include rem('margin-left', 6px);
        }
    }
}
