.sidebar-header {
    margin: 0;
    @include SourceSansProRegular();
    @include rem('font-size', 22px);
    line-height: (28px / 22px);

    &__toggle {
        color: $text_color;
        width: 100%;
        display: flex;
        justify-content: space-between;
        @include rem('padding-bottom', 3px);
        @include rem('border-bottom', 2px solid $link_color);

        &:hover {
            color: $text_color;
            text-decoration: none;
        }

        .arrow-icon {
            @include single-arrow-icon($direction: 'down', $color: $link-color, $size: 'large');
            @include rem('top', 16px);
            @include rem('right', 2px);
        }
    }

    &--open {
        & a {
            .arrow-icon {
                @include single-arrow-icon($direction: 'up', $color: $link-color, $size: 'large');
                @include rem('top', 16px);
                @include rem('right', 2px);
            }
        }
    }
}

