// CK editor
// -------------------------------------------------------------

.cke {
    border-color: $silver !important;
    @include border-radius(0 0 3px 3px !important);

    .cke_top {
        border-bottom-color: $silver !important;
    }
    .cke_bottom {
        border-top-color: $silver !important;
    }
    
    .cke_inner,
    .cke_contents,
    .cke_wysiwyg_frame,
    .cke_bottom {
        @include border-radius(0 0 3px 3px !important);
    }

    &.cke_focus .cke_contents {
    }
}

.cke_focus {
    .cke_inner {
        @include outline($outline-offset: 2px);
    }
}

// Excerpt counter for innbyggerhjelp
// Footer til CKE må styles om litt
// ----------------------------------------------------
.cke-excerpt-counter-container .cke_bottom {
    background-color: $white;
    border-top: 0 none !important;

    &:before {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        background-color: #f8f8f8;
        height: 100%;
        @include rem('width', 15px);
    }

    .cke_resizer_ltr {
        position: relative;
        @include rem('margin', 16px -5px 0 0);
    }

    .cke_path {
        // screen-reader-text
        clip: rect(1px, 1px, 1px, 1px);
        height: 1px;
        width: 1px;
        overflow: hidden;
        position: absolute !important;
    }

    .cke_wordcount {
        color: $grey7;
        @include rem('margin', 4px 14px 0px 0);

        .cke_path_item {
            font-family: 'Source Sans Pro', Verdana, sans-serif;
            @include SourceSansProRegular();
            @include rem('font-size', 17px);
            line-height: ( 24px / 17px );
            color: $grey7;

            &.cke_wordcountLimitReached {
                color: $brand_danger !important;
            }
        }
    }
}
