// Edit accordion // Avsnitt innbyggerhjelpen
// -------------------------------------------------------------

.edit-accordion {
    @include rem('padding', 21px);
    @include box-sizing();
    @include rem('border', 1px dashed $link_color);
    @include border-radius();
    @include rem('margin-bottom', 21px);

    &__bar {
        display: flex;
        justify-content: space-between;

        &__toggle {
            display: flex;
            justify-content: space-between;

            &:hover {
                text-decoration: none;
            }

            .arrow-icon {
                @include single-arrow-icon($direction: 'down', $color: $link-color, $size: 'large');
                @include rem('margin-left', 12px);
                @include rem('top', 14px);
                @include rem('right', 2px);
            }
        }

        &--open {
            .arrow-icon {
                @include single-arrow-icon($direction: 'up', $color: $link-color, $size: 'large');
                @include rem('margin-left', 12px);
                @include rem('top', 14px);
                @include rem('right', 2px);
            }
        }
    }

    // Flytt opp / ned
    &__sort {
        display: block;
        width: 100%;
        @include rem('padding', 7px 0 5px 14px);
        background-color: $water;
        @include border-radius();
        @include rem('font-size', 17px);
        line-height: ( 24px / 17px );

        &__link {
            position: relative;
            display: inline-block;
            @include rem('padding-left', 16px);
            @include rem('margin-right', 8px);
        }

        .icon-arrow-ordering {
            position: absolute;
            @include rem('top', -4px);
            @include rem('left', -7px);

            &--down {
                @include rem('top', -5px);
                @include rotate(180);
            }
        }
    }    
}
