.helpers_header {
  margin: 0;
  @include rem('font-size', 19px);
  line-height: (28px / 19px);
  @include SourceSansProSemibold();

  &--infobox {
    display: flex;
    justify-content: space-between;

  }
}

.helpers_info {
  position: relative;
  @include rem('font-size', 17px);
  line-height: (21px / 17px);
  color: $emperor;
  font-weight: 400;
}